<template>
  <div class="login-form-container">
    <div class="login-form-left">
    </div>
    <div class="login-form-right">
      <header class="row header">
        <div class="login-text">登录</div> 
      </header>
      <el-form ref="loginForm" :model="loginForm" class="form" label-position="top" :rules="rules">
        <el-form-item label="用户名" prop="userName" class="username-container">
          <el-input v-model="loginForm.userName"  @keypress.enter.native="login()"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" class="pwd-container">
          <el-input type="password" v-model="loginForm.password" @keypress.enter.native="login()"></el-input>
        </el-form-item>
        <div class="error-message" v-show="loginError">用户名或密码错误，请重新输入</div>
        <el-form-item class="auto-login">
          <el-checkbox v-model="loginForm.autoLogin">记住我</el-checkbox>
        </el-form-item>
        <el-form-item class="login-btn-container">
          <el-button @click="login()">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import authQuery from '@/rest/authQuery';
import localStorageHelper from '@/helpers/localStorageHelper';

const USER_AUTH_INFO_KEY = 'USER_AUTH_INFO';
export default {
  name: 'loginForm',
  data() {
    return {
      loginError: false,
      loginForm: {
        autoLogin: false,
        password: '',
        userName: ''        
      },      
      rules: {
        userName: [
          { required: true, message: '用户名不能为空', trigger: 'change' }
        ],
        password: [
          { required: false, message: '', trigger: 'change' }
        ]
      }
    };
  },
  methods: {
    login() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          // if valid, send request to server  
          authQuery.login(this.loginForm.userName, this.loginForm.password).then(() => {
            if (this.$route.query && this.$route.query.redirect) {
              this.$router.push({
                path: this.$route.query.redirect
              });
            } else {
              this.$router.push({
                path: '/'
              });
            }
            // if logged in successfully, then write auth into localstorage if autoLogin is true
            if (this.loginForm.autoLogin) {
              localStorageHelper.setItem(USER_AUTH_INFO_KEY, JSON.stringify({
                autoLogin: true,
                username: this.loginForm.userName,
                password: this.loginForm.password // TODO: use encoded string instead
              }));
            }           
          }, () => {
            this.loginError = true;
          });
        } 
      });
    }
  },
  mounted() {
    const userAuthInfo = JSON.parse(localStorageHelper.getItem(USER_AUTH_INFO_KEY));
    if (userAuthInfo) {
      this.loginForm = {
        autoLogin: userAuthInfo.autoLogin,
        userName: userAuthInfo.username,
        password: userAuthInfo.password
      };
      if (userAuthInfo.autoLogin) {
        this.$nextTick(() => this.login());
      }      
    }
  }
};
</script>

<style lang="scss" scoped>
@import '../../scss/color.scss';
@import '../../scss/mixin.scss';
.login-form-container {
  display: flex;
  width: 900px;
  border: solid 1px #e6edf0;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0px 10px 32px 0px rgba(0, 0, 0, 0.04);

  .login-form-left {
    width: 500px;
    background-image: url('./../../assets/icons/login-bg.svg');
    background-size: 100% 100%;
  }

  .login-form-right {
    width: 400px;
    padding: 26px 48px 4px 48px;
    border-left: solid 1px #e6edf0;
  }

  .header {
    align-items: center;   
    border-bottom: 1px solid #e4ecef;
    .login-text {
      font-size: 28px;
      color: #2b50bb;
      padding-bottom: 24px;
    }
  }
  .form {
    position: relative;
    margin-top: 1.5rem;
  }
  /deep/ {
    .el-form {
      .el-form-item {
        .el-form-item__label {
          padding: 0;
          height: 1.5rem;
          font-size: 0.875rem;
          color: #83898b;
          line-height: 1.5rem;
        }
        &.is-required .el-form-item__label:before{
          display: none;
        }
        .el-input__inner {
          margin-top: 0.5rem;
          height: 40px;
          font-size: 1rem;
          background-color: #ffffff;
          border-color: #e4ecef;
          line-height: normal;
          color: #333333;
          &:focus {
            border-color: #f3f6f7;
            background-color: #f3f6f7;
          }
        }
        &.is-success .el-input__inner {
          border-color: #f3f6f7;
          background-color: #f3f6f7;
        }
        &.is-error .el-input__inner {
          border-color: #f27272;
        }
        &.pwd-container {
          margin-top: 1.5rem;
          margin-bottom: 0;
        }
        &.auto-login {
          margin-top: 1.5rem;
        }
        &.login-btn-container{
          margin-top: 1rem;
          .el-button {
            width: 100%;
            height: 3rem;
            font-size: 1rem;
            border-radius: 0.3rem;
            color: white;
            background-color: #53b1f1;
            &:hover{
              background-color: #7ac9ff;
            }
          }          
        }
      }
      .error-message {
        position: absolute;
        margin-top: 0.125rem;
        padding-left: 0.375rem;
        width: 25.375rem;
        height: 1.5rem;
        font-size: 0.875rem;
        color: #f27272;
        background-color: transparent;
        border-radius: 0.25rem;
        line-height: 1.5rem;        
      }
    }    
  }
}
</style>
